import { useContext } from "react";
import { ModalContext } from "./Context/ModalProvider";

function Use() {
    const {setisShow} = useContext(ModalContext)
  return (
    <div className="flex flex-col items-center justify-center ">
      <div className="bg-white shadow-lg rounded-lg p-2 max-w-xl w-[90%]">
      <p className="float-end rounded-full cursor-pointer text-[10px] bg-gray-400 w-5 h-5 flex items-center justify-center text-center" 
      onClick={()=>setisShow(false)}>X</p>
        <p className="font-medium mt-5 pt-2" style={{ textIndent: "20px" }}>
          Chào các sếp, Check Qr trả sau Momo chỉ hỗ trợ check 1 list số cùng 1
          mật khẩu đối tác, 1 list số chỉ có nhiều mật khẩu sẽ check không được.
        </p>
        <div className="font-light">
        <p className="font-medium">Bước 1:</p>
        <p style={{ textIndent: "20px" }}>-Chuẩn bị và copy list SĐT và Mật Khẩu chung (các tài khoản cùng 1 mật khẩu).</p>
        <p className="font-medium"> Bước 2:</p>
        <p style={{ textIndent: "20px" }}>
          - Chọn ngày cần check và dán SĐT và Mật Khẩu vào ô Input. Xong Bấm Add SĐT.{" "}
        </p>
        <p className="font-medium">Bước 3:</p>
        <p style={{ textIndent: "20px" }}>
          - Bấm Check. Check lần 2 trở lên bạn cần Clear Data trước khi nhập
          list số khác.
        </p>
        </div>
        <p className="mt-4 pb-10 text-[14px] text-red-600">Hiện tại mình đang dùng gói 7$/tháng để chạy mượt. Nếu giúp ích cho mọi người thì góp cafe với em nha. Mình xin nhận mọi đóng góp để cải thiện hiệu năng, chức năng...  <span className="text-red-500 font-medium">0362061339</span> zá lồ. Chúc các sếp kiếm thật nhiều tiền </p>
      </div>
    </div>
  );
}

export default Use;